<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-5 gap-6">
    <div class="col-span-3">
      <div class="p-4 card xl:p-5">
        <form
          @input="request.errors.clear($event.target.name)"
          @submit.prevent="submit()"
        >
          <div class="space-y-4">
            <div>
              <label for="textName">{{ $t("attributes.name") }}</label>
              <span class="relative flex mt-1.5">
                <input
                  v-model="workspace.name"
                  id="textName"
                  name="name"
                  type="text"
                  autocomplete="off"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                />
                <span
                  class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                >
                  <v-icon name="swatch" size="6"></v-icon>
                </span>
              </span>

              <validation-feedback>
                {{ request.errors.get("name") }}
              </validation-feedback>
            </div>

            <div>
              <label for="fileLogo">{{ $t("attributes.logo") }}</label>
              <span class="flex mt-1.5">
                <input
                  ref="logoInput"
                  id="fileLogo"
                  name="logo"
                  type="file"
                  accept="image/*"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  @input="fileSelected"
                />
              </span>

              <div class="mt-2">
                <v-button
                  v-if="imageSrc"
                  key="isLogoSelected"
                  color="transparent"
                  class="px-0"
                  @click="clearLogo"
                  >Logoyu KaldÄ±r
                </v-button>
              </div>

              <validation-feedback>
                {{ request.errors.get("logo") }}
              </validation-feedback>
            </div>

            <table
              class="w-full table-auto border-separate border-spacing-4 rounded-lg border border-slate-200 dark:border-navy-600"
            >
              <thead>
                <tr>
                  <th class="w-1/2 text-start font-medium">
                    {{ $t("attributes.group_name") }}
                  </th>
                  <th class="w-1/2 text-start font-medium">
                    {{ $t("attributes.group_size") }}
                  </th>
                  <th>
                    <div class="w-9"></div>
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(group, index) in workspace.groups" :key="index">
                  <td>
                    <input
                      v-model="group.name"
                      :name="`group${index}Name`"
                      :id="`textGroup${index}Name`"
                      type="text"
                      autocomplete="off"
                      class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                    />
                  </td>
                  <td>
                    <input
                      v-model="group.size"
                      :name="`group${index}Size`"
                      :id="`textGroup${index}Size`"
                      type="number"
                      min="0"
                      autocomplete="off"
                      class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                    />
                  </td>
                  <td>
                    <v-button
                      color="transparent"
                      icon
                      :title="$t('labels.delete')"
                      @click="workspace.removeGroup(index)"
                    >
                      <i class="fa fa-lg fa-times"></i>
                    </v-button>
                  </td>
                </tr>
              </tbody>

              <tfoot>
                <tr>
                  <td colspan="3">
                    <v-button
                      color="transparent"
                      block
                      weight="normal"
                      align="end"
                      @click="workspace.addGroup()"
                    >
                      <span class="mr-2">{{ $t("labels.add_group") }}</span>
                      <i class="fa fa-lg fa-plus"></i>
                    </v-button>
                  </td>
                </tr>
              </tfoot>
            </table>

            <div class="text-end">
              <v-button
                type="submit"
                color="primary"
                :disabled="!request.submittable"
              >
                {{ $t("labels.submit") }}
              </v-button>
            </div>
          </div>
        </form>
      </div>
    </div>

    <div class="col-span-2">
      <div class="p-4 card xl:p-5">
        <div class="bg-slate-100 p-5 dark:bg-slate-300">
          <div class="inline-block">
            <img
              v-if="imageSrc"
              key="isWorkspaceHasLogo"
              :src="imageSrc"
              :title="$t('labels.uploaded_image')"
              :alt="$t('labels.description_image')"
              class="h-16 w-16 rounded-lg border border-slate-200 object-cover"
            />
            <div
              v-else
              class="flex h-16 w-16 items-center justify-center rounded-lg border border-slate-200 bg-white dark:text-navy-600 dark:border-navy-600"
            >
              <v-icon name="swatch" size="11"></v-icon>
            </div>
          </div>

          <p
            class="mt-3 text-lg font-medium text-slate-600 line-clamp-1 dark:text-navy-600"
          >
            {{
              workspace.name ? workspace.name : $t("placeholder.workspace_name")
            }}
          </p>
          <div class="mt-10 flex justify-between">
            <div class="flex -space-x-2">
              <div
                v-for="(group, index) in workspace.groups"
                :key="index"
                class="h-9 w-9 avatar hover:z-10"
              >
                <div
                  class="is-initial rounded-full bg-primary text-xs+ uppercase text-white ring ring-white dark:ring-primary-focus"
                >
                  {{ group.nameLetters }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ModelRequest from "@/request/ModelRequest";
import Workspace from "@/models/Workspace";

export default {
  name: "AppWorkspaceEdit",
  props: {
    id: {
      type: [Number, String],
      required: true,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.workspaces"), route: "app.workspaces" },
        {
          title: this.$t("models.workspace.edit"),
          active: true,
        },
      ],
      imageSrc: null,
      workspace: new Workspace(),
      request: new ModelRequest(),
    };
  },
  created() {
    this.workspace.fetch(this.id).then(() => {
      this.imageSrc = this.workspace.logoSrc;
    });

    this.request.bind(this.workspace);
  },
  methods: {
    async fileSelected(event) {
      this.imageSrc =
        (await this.workspace.setLogoFile(event)) ?? this.imageSrc;
    },
    clearLogo() {
      this.imageSrc = null;
      this.workspace.logo = null;
      this.$refs.logoInput.value = null;
    },
    submit() {
      this.request.update({
        redirect: { name: "app.workspaces" },
      });
    },
  },
};
</script>